<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">发布文章</p>
        </div>
        <div class="setBox">
            <div class="setTitle pl-30 f28 c333">
                标题
                <input type="text" v-model="data.title" class="oinp" placeholder="请输入">
            </div>
            <div class="home-comntent">
                <p class="pl-30 f28 c333 pt-20 pb-30">内容</p>
                <textarea name="" id="" v-model="data.resourceDesc" cols="30" class="textarea f26 pl-30 pr-30 pt-10" rows="10" placeholder="请输入帖子内容"></textarea>
            </div>
            <div class="upload">
                <p class="pl-30 f28 c333 pt-20 pb-30">图片</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in data.imageUrls" :key="index">
                        <div class="imgShow">
                            <img :src="$api.fileUrl + item" alt="">
                            <span @click="deleteThisImg(index)" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox" v-if="data.imageUrls.length < 10">
                        <van-uploader :after-read="onRead" accept="image/png, image/jpeg">
                            <img class="imgShow" src="../../../assets/my/upload.png" alt="">
                        </van-uploader>
                    </div>
                </div>
            </div>
            <div class="upload mt-30">
                <p class="pl-30 f28 c333 pt-20 pb-30">视频</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in data.videoUrls" :key="index">
                        <div class="imgShow">
                            <video :src="$api.fileUrl + item" alt=""></video>
                            <span @click="deleteThisVideo(index)" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox" v-if="data.videoUrls.length < 1">
                        <van-uploader :after-read="onReadV" accept="video/*">
                            <img class="imgShow" src="../../../assets/my/upload.png" alt="">
                        </van-uploader>
                    </div>
                </div>
            </div>
            <!-- <div style="height:1.8rem;"></div> -->
            <div class="centerBtn f26 cfff text-center" @click="saveOrUpdateTeachResource">发布</div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            chooseClass:'选择班级',
            chooseSubject:'选择科目',
            modelShow:false,
            modelShow1:false,
            data:{
                imageUrls:[],
                videoUrls:[],
                resourceDesc:'',
                title:''
            }
        }
    },
    methods : {
       returns(){
            this.$router.go(-1);//返回上一层
        },
        deleteThisVideo(index){
            this.data.videoUrls.splice(index,1);
        },
        deleteThisImg(index){
            this.data.imageUrls.splice(index,1);
        },
        next(){//跳转到下一个页面
            this.$router.push({path:'/teachingresourcesdetail'});
        },
        onRead(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                if(data.code == 0){
                    this.data.imageUrls.push(data.data[0]);
                }
            })
        },
        onReadV(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                if(data.code == 0){
                    this.data.videoUrls.push(data.data[0]);
                }
            })
        },
        getAllResourceDetail(){//获取详情
            this.$toast.loading({
                mask: true,
                duration:0
            });
            this.$http({url:this.$api.teachingresources.getAllResourceDetail},{id:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
                
            },err =>{
                this.$toast.clear();
            })

        },
        saveOrUpdateTeachResource(){
            this.$toast.loading({
                mask: true,
                duration:0
            });
            var param = {
                title : this.data.title,
                resourceDesc : this.data.resourceDesc,
                imageUrl : this.data.imageUrls.toString(),
                videoUrl : this.data.videoUrls.toString(),
                teacherId : JSON.parse(localStorage.getItem('zhxy_user')).id,
                schoolId : JSON.parse(localStorage.getItem('zhxy_user')).schoolId,
                origin : 1
            };
            if(this.$route.query.id){
                param.id = this.$route.query.id
            }
            this.$http({url:this.$api.teachingresources.saveOrUpdateTeachResource},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$router.push({path:'/teachingresources'});
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast.clear();
            })
        }

    },
    created() {
        if(this.$route.query.id){
            this.getAllResourceDetail();
        }
    },
}
</script>

<style scoped>
.loginNavFix{z-index: 999;}
.setBox{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-y: auto;}
.setTitle{height: 1.11rem;border-bottom: 1px solid #f2f2f2;line-height: 1.11rem;overflow: hidden;}
.oinp{float: right;border: 0;outline: 0;width: 80%;text-align: right;padding-right: 0.2rem}
.home-comntent{height: 3.74rem;border-bottom: 1px solid #f2f2f2;}
.textarea{width: 100%;height: 1.1rem;outline: 0;border: 0;box-sizing: border-box;resize:none;}
.iploadBox{margin-left: 0.3rem;overflow: hidden;}
.imgShowBox{width: 20%;float: left;}
.imgShow{width: 1.2rem;height: 1.2rem;border-radius: 5px;overflow: hidden;float: left;position: relative;}
.imgShow img{width: 100%;height: 100%;display: block;}
.imgShow video{width: 100%;height: 100%;display: block;}
.closeX{position: absolute;top: 0;right: 0;padding: 0.05rem 0.1rem;}
.borderTop{border-top: 1px solid #f2f2f2;}
.msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
.centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;margin: 0.6rem auto }

</style>





